<template>
  <v-menu
    bottom
    left
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn
        class="ml-2"
        min-width="0"
        text
        v-bind="attrs"
        v-on="on"
      >
        <v-icon>mdi-translate</v-icon>
      </v-btn>
    </template>

    <v-list
      :tile="false"
      flat
      nav
    >
      <template v-for="(p, i) in profile">
        <v-list-item
          :key="`item-${i}`"
          @click="changeLocale(p.lang)"
        >
          <v-list-item-title v-text="$vuetify.lang.t(p.text)" />
        </v-list-item>
      </template>
    </v-list>
  </v-menu>
</template>

<script>
  export default {
    name: 'DefaultLanguage',

    data: () => ({
      profile: [
        { text: '$vuetify.zhHant', lang: 'zhHant' },
        { text: '$vuetify.en', lang: 'en' },
      ],
    }), 

  methods: {
    changeLocale(lang) {
      this.$vuetify.lang.current = lang;
      localStorage.setItem('lang',this.$vuetify.lang.current)
      //location.reload();
    }
  }    
  }
</script>
